import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './index.css';
export class Time extends Component {
  state={
    time:''
  }
   componentDidMount()
   {
    let f=new Date()
    this.setState({
      time: f.toDateString()+" "+f.toLocaleTimeString()
    });
    setInterval(() => {
    let f=new Date()
    this.setState({
      time: f.toDateString()+" "+f.toLocaleTimeString()
    });
    }, 1000);
    
   }
  render() {
    return (
      <div className="borx time">
        {this.state.time}
      </div>
    );
  }
}

export class Task extends Component {
 
  render()
  {
    const {item}=this.props;
    let clas='task'+''+ (item.done?' done':' undone') ;
    return <div className={clas} onClick={(e)=>item.togglework(item.index,e)} >
      <div className="work"> {item.work}</div>
      <div className="remove" title='remove this task' onClick={(e)=>item.remove(item.index,e)} >
      </div>
    </div>
  }
}

export class Todo extends Component {
  state={task:[]};
  input=React.createRef();
  togglework=(i,ee)=>
  {
    if (ee.target.className=="remove") {
      return;
    }
    let list =[...this.state.task];
    let e=list[i];
    e.done=! e.done;
    this.setState({task:list},(e)=>
      {
        localStorage.setItem("tds",JSON.stringify(this.state.task));
      })
  }
  remove=(i,ee)=>
  {
    let list =[...this.state.task];
    list.splice(i,1);
    this.setState({task:list},(e)=>
      {
        localStorage.setItem("tds",JSON.stringify(this.state.task));
      })
  }
  componentDidMount()
  {
    let l=localStorage["tds"];
    if (!l) {
      localStorage.setItem("tds",JSON.stringify([
        {
          work:"build Crome extension.",
          done:true
        },
        {
          work:"upload Crome extension.",
          done:true
        },
        {
          work:"Use Crome extension.",
          done:false
        }
      ])
      );
    } else
    {
      this.setState({task:JSON.parse(l)});
    }
  }
  enter=(e)=>
  {
console.log(this.input);
    if (e.key=='Enter' && e.target.value!='') {
      this.setState({task:this.state.task.concat({work:e.target.value,done:false})},(e)=>
      {
        localStorage.setItem("tds",JSON.stringify(this.state.task));
        this.input.current.value='';
      })
    }
  }
  render()
  {
    return   <div className="todo borx" >
      <div className="title"><h1>Today's Goal :({this.state.task.length})</h1></div>
      <div className="list">
          {this.state.task.map((r,i)=> <Task key={i} item={{...r,index:i,togglework:this.togglework,remove:this.remove}} /> )}
      </div>
      <div className="add">
        <input type="text" ref={this.input}  onKeyPress={(e)=>this.enter(e)} />
      </div>
    </div>;
  }
}
function App() {
    return (
     <>
     <div className="body">
<div className="bgg"></div>
<div id="container">
  <Time />
  <Todo />
</div>
</div>
     </>
    );
  }
  


ReactDOM.render(<App />, document.getElementById('root'));
